@font-face {
    font-family: "DejaVuSans-ExtraLight";
    src: url('../../Fonts/DejaVuSans-ExtraLight.ttf');
    font-weight: normal;
    font-style: normal;

}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    background-color: #415063;
    height: 100vh;
    /* display: grid; */
    /* place-items: center center; */
    /* display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; */
}
.Container{
    display: flex;
    flex-direction: column;
    justify-content: center; 
    align-items: center;
}
.Container .cards{
    /* z-index: 1; */
    width: 80%;
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px,1fr));
    padding: 20px;
    row-gap: 40px;
    justify-items: center;
    align-items: center;
}
.input{
    height: 100%;
    display: flex;
    flex: 3;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    transition: all .2s ease-in-out;
    /* border: red solid 1px; */
}
input{
    border-radius: 0;
    border: none;
    background-color: #303f4b;
    width: 150px;
    min-width: 150px;
}
input:focus{
    outline: none;
}
input::placeholder {
    color: rgb(212, 213, 216);
    padding-left: 5px;
    font-size: 16px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
input:focus::placeholder {
    color: transparent;
  }
.Container.active{
   display: none;
}
.Container .cards .card{
    cursor: pointer;
    width: 9vw;
    min-width: 150px;
    max-width: 160px;
    height: 270px;
    background-color: rgb(255, 255, 255);
    border-radius: 7px;
    box-shadow: 0 0 30px rgba(0,0,0,0,17);
    border: rgb(255, 255, 255) solid 1px;
}
.Container .cards .card .container{
    /* border: green solid 2px; */
    position: initial;
    height: 75%;
    
    /* clip-path: polygon(0 0,100% 0,100% 85%, 0 100%); */
}
.Container .cards .card .container img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* border: rgb(238, 58, 148) solid 1px; */
    
    display: block;
    border-radius: 8px 8px 0 0;
    z-index: -5;
}



.Container .cards .details {
    height: 20%;
    padding: 10px 5px;
    text-align: center;
    display: grid;
    justify-content: center;
}

.Container .cards .details>h3 {
    font-family: "ARMTMS", Tahoma;
    font-weight:500;
    height: 100%;
    color: rgb(0, 0, 0);
    font-size: 14px;
}

.Container .cards .details>p {
    font-family: "ARMTMS", Tahoma;
    font-weight: 200;
    height: 100%;
    color: rgb(82, 82, 82);
    font-size: 12px;
    line-height: 16px;
    font-weight: 400;
    font-style: italic;
    padding-top: 5px;

}


